  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.vis-time-axis .vis-text {
  position: absolute;
  color: #4d4d4d;
  padding: 3px;
  overflow: hidden;
  box-sizing: border-box;
  white-space: nowrap;
}
.vis-time-axis .vis-text.vis-measure {
  position: absolute;
  padding-left: 0;
  padding-right: 0;
  margin-left: 0;
  margin-right: 0;
  visibility: hidden;
}
.vis-time-axis .vis-grid.vis-vertical {
  position: absolute;
  border-left: 1px solid;
}
.vis-time-axis .vis-grid.vis-vertical-rtl {
  position: absolute;
  border-right: 1px solid;
}
.vis-time-axis .vis-grid.vis-minor {
  border-color: #e5e5e5;
}
.vis-time-axis .vis-grid.vis-major {
  border-color: #bfbfbf;
}
.vis-timeline {
  position: relative;
  border: 1px solid #bfbfbf;
  overflow: hidden;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.popover-header {
  padding: 2px;
  margin-bottom: 0;
  font-size: 11px;
  background-color: #f7f7f7;
  border-bottom: 1px solid #ebebeb;
  border-top-left-radius: calc(0.3rem - 1px);
  border-top-right-radius: calc(0.3rem - 1px);
  text-align: center;
}

.popover-body {
  padding: 2px;
  font-size: 10px;
  color: #212529;
}

#group_filter {
  cursor: pointer;
}

.event-filter-label {
  font-size: 13px;
  color: grey;
  margin-left: 22px;
  margin-bottom: 22px;
}

/* ITEMS */

.vis-dot {
  display: none;
}

.vis-line {
  display: none;
}

.vis-item.vis-range {
  background-color: rgba(0, 0, 0, 0);
  box-sizing: border-box;
  height: 10px;
  border: 0px;
  border-radius: 2px;
  border-style: solid;
  padding: 1.5px;
}

.vis-item.vis-range .vis-item-overflow {
  /* background-color: white; */
  border: 0;
  height: 9px;
  border-radius: 2px;
}

.vis-item.vis-range .vis-item-overflow .vis-item-content {
  border: 0;
}

.vis-item.no-sur .vis-item-content {
  background-image: radial-gradient(white, rgba(0, 0, 0, 0));
  border-radius: 6px;
}

.vis-item.no-sur-legend .vis-item-content {
  background-image: radial-gradient(white, rgba(0, 0, 0, 0));
  border-radius: 6px;
}

.second_click {
  z-index: 100;
}

.first_click {
  z-index: 100;
}

/* LEGEND */

.vis-item.legend {
  background-color: darkgrey;
  border-color: grey;
}

.vis-item.vis-range.legend {
  background-color: rgba(0, 0, 0, 0);
}

.vis-item.vis-range.sipt--long-sur {
  background-color: grey;
}

.vis-item.vis-range.sipt--no-end {
  background-color: rgba(0, 0, 0, 0);
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0), grey 50%);
}

.vis-item.vis-range.sipt--no-beg {
  background-color: rgba(0, 0, 0, 0);
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), grey 50%);
}

.vis-item.vis-range.sipt--no-thi {
  background-color: rgba(0, 0, 0, 0);
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    grey 40%,
    grey 60%,
    rgba(0, 0, 0, 0)
  );
}

.vis-item.vis-range.sipt--long-thi .vis-item-overflow {
  background-color: rgba(0, 0, 0, 0);
  overflow: visible;
  top: 4px;
}

.vis-item.vis-range.sipt--long-thi .vis-item-overflow .vis-item-content {
  background-color: darkgrey;
  border-color: grey;
  border-width: 1px;
  border-style: solid;
  border-radius: 2px;
  overflow: visible;
  height: 6px;
}

.vis-item.vis-range.legend .click-content {
  background-color: darkgrey;
}

.vis-item.vis-range.sipt--no-end .click-content {
  background-color: rgba(0, 0, 0, 0);
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0), darkgrey 50%);
}

.vis-item.vis-range.sipt--no-beg .click-content {
  background-color: rgba(0, 0, 0, 0);
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), darkgrey 50%);
}

.vis-item.vis-range.sipt--no-thi .click-content {
  background-color: rgba(0, 0, 0, 0);
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    darkgrey 40%,
    darkgrey 60%,
    rgba(0, 0, 0, 0)
  );
}

.vis-item.vis-range.sipt--long-thi .click-content {
  width: 10px;
  margin: auto;
  height: 12px;
  position: relative;
  top: -4px;
  left: -0.5px;
  border-radius: 6px;
  border-top: 1px grey solid;
  border-bottom: 1px grey solid;
}

.vis-item.vis-box {
  width: 12px;
  height: 10px;
  border-radius: 100%;
}

.vis-custom-time {
  background-color: #6c757d;
}
